.ProjectCardBase {
    padding: 0;
    width: 401px;
    height: 409px;
    background: #fff;
    border-radius: 15px;
    box-shadow: 10px 10px 0px rgba(0, 0, 0, 0.25);
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}

.ProjectCardBase *{
    color: #1D2951;
}

.ContentProjectCard {
    padding: 0px;
    width: 372px;
    height: 378px;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
}

.ProjectCardBase img {
    width: 350px;
    height: 215px;
    border-radius: 10px;
}

.ProjectCardText {
    width: 372px;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    text-align: justify;
}

.ProjectCardText h1 {
    margin-top: 10px;
    font-size: 20px;
    align-self: flex-start;
    text-align: justify;
}

.ProjectCardText p, .GitHubLink{
    font-size: 10px;
    line-height: 18px;
    margin-top: 5px;
    align-self: flex-start;
    text-align: justify;
    transition: 0.3s;
}

.GitHubLink {
    margin-top: 8px;
}

.GitHubLink a {
    color: #1D2951;
    text-decoration: none;
    display: flex;
    align-items: center;
    transition: color 0.3s;
}

.GitHubLink a:hover {
    color: #B9EEE9;
}


.ProjectCardIcon {
    width: 13px;
    height: 13px;
}